.tile {
    position: absolute;
    width: 85px;
    height: 85px;
    border: 3px solid black;
    border-radius: 5px;
    text-align: center;
    line-height: 85px;
    margin: 5px;
}


/*   animations   */

.merge-enter {
    opacity: 0;
    -webkit-transform: scale(.5);
        -ms-transform: scale(.5);
            transform: scale(.5);
}

.merge-enter.merge-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.merge-leave {
    opacity: 1;
}

.merge-leave.merge-leave-active {
    opacity: .5;
    -webkit-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;
}

.tile-enter {
    opacity: .8;
}

.tile-enter.tile-enter-active {
    opacity: 1;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.tile-leave.tile-leave-active {
    opacity: 0;
}


